.formContainer {
    width: 1600px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.testTerminal {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 25px;
    padding-bottom: 10px;
    padding-top: 10px;
    background-color: #1d2025;
    font-size: 18px;
    border-radius: 10px;
    border: black solid 2px;
}

select.strategy  {
    width: 250px
}

.strategyPanel {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.fetch {
    display: flex;
    flex-direction: row;
    width: 50%;
    height: 100%;
    margin-bottom: 10px;
}

.paramCont {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}

.leftCol {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.rightCol {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.actionsRow {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
}

.actionButton {
    height: 25px;
    width: 25%;
    margin: 2px 0px;
    background-color: #3d4147;
    color: white;
}

.smaInput {
    width: 75%;
    height: 25px;
    color: black;
    font-size: 18px;
    margin: 2px 0px;
}

.calendarInput {
    width: 210px;
    height: 25px;
    margin: 2px 0px;
    color: black;
}

.fetchStratButton {
    height: 32px;
    width: 25%;
    margin: 2px 0px;
    background-color: #3d4147;
    color: white;
}

.canvas {
    padding-right: 20px;
}

.chartContainer {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    clear:both;
    width: 1600px;
    height: 650px;
    border: black 2px solid;
}

.orders {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 20%;
    border-left: black 2px solid;
    margin-left: 10px;
    overflow-y: scroll;
    overflow-x: hidden;
}

.trade {
    color: white;
    width: 100%;
    font-size: 12px;
    display: flex;
    flex-direction: column;
    border: black 1px solid;
}

.trade > p {
    margin-left: 5px;
}

.gain {
    background-color: rgba(0, 255, 0, 0.25);
}

.loss {
    background-color: rgba(255,0,0,0.25);
}

.account {
    position: sticky;
    top:0;
    background-color: #1d2025;
}

.balance {
    border-bottom: black 2px solid;
    background-color: orange;
}

.foo {
    background-color: red;
}